
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import http from '@/http.js'
import qs from 'qs'

export default {
components: {
XInput,
XButton,
Group,
Cell,
ViewBox,
Tabbar, TabbarItem
},
name: 'app',
data:function(){return{

islogin:false,
user_sn:null,
password:null,
}},

methods:{
login(){
var self = this
http.post('/login/',{
username:this.user_sn,
password:this.password
},{
transformRequest: [function (data) {

return qs.stringify(data)}],
},).then((response)=>{
localStorage.setItem('token',response.data.token)
http.defaults.headers.common['Authorization'] = 'JWT '+response.data.token;
localStorage.setItem('username',this.user_sn)
this.islogin = true

})
.catch((error)=>{
if(error.response.data.username){
self.$vux.alert.show({'title':'提醒',content:'账号不能为空'})
}else if(error.response.data.password){
self.$vux.alert.show({'title':'提醒',content:'密码不能为空'})
}else{
self.$vux.alert.show({'title':'提醒',content:'账号密码错误'})
}

this.user_sn = ''
this.password = ''
})
},
reflesh(){
var self = this
http.post('/refresh/',{token:localStorage.getItem('token')},{
transformRequest: [function (data) {

return qs.stringify(data)}],
}).then(response=>{
localStorage.setItem('token',response.data.token)
http.defaults.headers.common['Authorization'] = 'JWT '+response.data.token;
this.islogin = true
this.$router.push({path:'/message'})

}).catch(error=>{
this.user_sn = null
this.password = null
this.islogin = false
})
},
change(i){
if(i==0){
this.$router.push({ path: `/message` })
}
else if(i==1){
this.$router.push({path:`books`})
}else if(i==2){
this.$router.push({path:'my'})
}
this.$store.commit('go_tab',i)
}
},
computed: {

index:function(){
return this.$store.state.TAB
}

},
mounted(){

this.reflesh()







},
}



