
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import http from '@/http'

export default{
components: {
Search,
Panel,
CellBox,
Group,
LoadMore,
Actionsheet,
XDialog
},
name:'books',
data:function(){return{

show:false,
tmp:{category:{}},
next:'',
value:'',
results:[],
list:[]
}},
methods:{
onSubmit(){
console.log(this.value)
http.get('/book/',{params:{search:this.value}}).then((r)=>{
this.next = r.data.next
this.list = r.data.results
})
},
info(v){
this.tmp = v
this.show = true
},
get_more_book(){
http.get(this.next).then((r)=>{
this.next = r.data.next
this.list = this.list.concat(r.data.results)
})
}
},
mounted:function(){
if(this.$store.state.TAB != 1){
this.$store.commit('go_tab',1)
}
}

}



