
import http from '@/http'
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default{
components: {
Panel,
Group,
Radio,
LoadMore
},
name:'message',
data:function(){return{
count:0,
next:'',
prev:null,
more:'没有更多数据',
list:[

]
}},
methods:{
get_message(){
http.get('/news/').then((response)=>{
let data = response.data
this.count = data.count
this.next = data.next
this.prev = data.previous
let tmp_data = []
let rd = data.results
for(var i in rd){
console.log(i)
tmp_data.push({
title:rd[i].title,
desc:rd[i].content,
meta:{date:rd[i].add_time,source:rd[i].author},
})


}
this.list = tmp_data
console.log(this.list)
if(this.next){
this.more = '点击加载更多数据'
}else{
this.more = '没有更多数据'
}
})
},
get_more_message(){
http.get(this.next).then((response)=>{
let data = response.data
this.count = data.count
this.next = data.next
this.prev = data.previous
let tmp_data = []
let rd = data.results
for(var i in rd){
console.log(i)
tmp_data.push({
title:rd[i].title,
desc:rd[i].content,
meta:{date:rd[i].add_time,source:rd[i].author},
})


}
this.list = this.list.concat(tmp_data)
console.log(this.list)
if(this.next){
this.more = '点击加载更多数据'
}else{
this.more = '没有更多数据'
}

})
}
},
mounted:function(){
if(this.$store.state.TAB != 0){
this.$store.commit('go_tab',0)
}
this.get_message()
}
}



